.App{
  height: 100%;
  width:100%;
  display:-ms-flexbox;
  display:flex;
  -ms-flex-direction: column;
      flex-direction: column;
  background-color: #aaa;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
}

.clearFix{
  clear:both;
}

#root{
  max-width: 400px;
  height: 100%;
  margin: 0px auto;
}

body , html {
  width: 100%;
  height: 100%;
  padding: 0px;
  margin: 0px;
}

.App > .inputBox{
  height:auto;
  -ms-flex-negative: 0;
      flex-shrink: 0;
  overflow-y: auto;
  overflow-x: hidden;
  width: 100%;
}

.App > .MessageBox{
  width: 100%;
  height:100%;
  -ms-flex-negative: 1;
      flex-shrink: 1;
  overflow-x: hidden;
  padding: 5px;
  padding: 10px;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
}

.App > .NotificationBox{
  -ms-flex-negative: 0;
      flex-shrink: 0;
  background-color: #F1F1F1;
  padding: 4px;
}




